<template>
  <div class="yinMenuItem">
<!--       @mouseover="$emit('mouseover',$event)"-->
<!--       @mouseout="$emit('mouseout',$event)"-->
<!--       @click="$emit('click',$event)">-->
    <div class="yinMenuItemMain">
      <div class="main">
        <n-icon size="21" class="fnIcon" v-if="$slots.icon">
          <slot name="icon"/>
        </n-icon>
        <p>
          <slot/>
        </p>
      </div>
      <div class="children nodeMenu" v-if="$slots.children">
        <div class="itemList">
          <slot name="children"/>
        </div>
      </div>
      <p>
        <slot name="mate"/>
      </p>
    </div>
    <p>
      <slot name="info"/>
