</template>

<style lang="less">
.yinMenuItem {
  user-select: none;
  cursor: pointer;
  padding: 8px 9px;
  border-radius: var(--border-radius-inner);
  font-size: 14px;

  .yinMenuItemMain {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;


    .main {
      display: flex;
      align-items: center;
      margin-right: 8px;

      .fnIcon {
        margin-right: 8px;
      }
    }

    & > p {
      font-size: smaller;
      opacity: .8;
    }


    .children {
      display: none;
      left: calc(100% + 3px);
      top: 0;
    }
  }

  & > p {
    transition: all 0.2s 0.2s;
    font-size: smaller;
    margin-left: 29px;
    line-height: 0;
    opacity: 0;

  }

  &:hover {
    background-color: var(--color-background-mute);

    .yinMenuItemMain {
      .children {
        display: unset;
      }
    }

    & > p {
      line-height: unset;
      opacity: 0.8;
    }
  }

}
</style>
